// ** Custom Components
import Avatar from '@components/avatar'
import { Badge } from 'reactstrap'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.clientname?.charAt(0) || 'T'}
    />
  )
}
const paymentstatusObj = [
  { id: '', name: "Select All" },
  { id: '5', name: "Pending", color: 'light-danger' },
  { id: '6', name: "Partially Paid", color: 'light-warning' },
  { id: '7', name: 'Paid', color: 'light-success' },
  { id: '11', name: 'Sent', color: 'light-secondary' }
]
const statusObj = [
  '',
  'light-primary',
  'light-warning',
  'light-success',
  'light-secondary',
  'light-secondary',
  'light-ocean',
  'light-info',
  'light-danger'
]

const statusOptions = ['', 'Pending', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']


export const columns = [
  {
    name: 'Task ID',
    sortable: true,
    maxWidth: '388px',
    sortField: 'uniqueidentity',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center text-overflow-ellipsis'>
        <div className='d-flex flex-column'>
          <div className='user_name text-truncate text-body'>
            <span className='fw-bolder'>{row.uniqueidentity}</span>
          </div>
        </div>
      </div>
    )
  },
  {
    name: 'Client',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.clientname,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center text-overflow-ellipsis'>
        {renderClient(row)}
        <div className='d-flex flex-column'></div>
        <div className='user_name text-truncate text-body'>
          <span className='text-capitalize'>{row.clientname}</span>
        </div>
      </div>
    )
  },
  {
    name: 'Service',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.todotasks,
    cell: row => (
      <div className='user_name text-truncate text-body'>
        <span className='text-capitalize'>{row.servicename}</span>
      </div>
    )
  },
  {
    name: 'Assignee',
    minWidth: '138px',
    sortable: false,
    cell: row => <div >
      {row.assignee && (
        row.assignee.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj?.name?.split(' ')[0]}</Badge>) })
      )}
    </div>

  },
  {
    name: 'Reviewer',
    minWidth: '138px',
    sortable: false,
    cell: row => (
      <div >
        {row.reviewer && (
          row.reviewer.map((obj, key) => { return (<Badge color='light-primary' key={key}>{obj?.name?.split(' ')[0]}</Badge>) })
        )
        }
      </div>
    )
  },
  {
    name: 'Task Status',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.requestchangestasks,
    cell: row => (
      <div className='user_name text-truncate text-body'>
        <Badge color={statusObj[row.taskstatus]}>{statusOptions[row.taskstatus]}</Badge>
      </div>
    )
  },
  {
    name: 'Invoice Id',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.totaltasks,
    cell: row => (
      <div className='user_name text-truncate text-body'>
        <span className='text-capitalize'>{row.uniqueno}</span>
      </div>
    )
  },
  {
    name: 'Amount (Inclusive of GST)',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.totaltasks,
    cell: row => (
      <div className='user_name text-truncate text-body'>
        <span className='text-capitalize'>{row.totalamount}</span>
      </div>
    )
  },
  {
    name: 'Payment Status',
    sortable: true,
    maxWidth: '168px',
    sortField: 'clientname',
    selector: row => row.totaltasks,
    cell: row => (
      <div className='user_name text-truncate text-body'>
        <span className='text-capitalize'>{paymentstatusObj.find((obj) => parseInt(row.paymentstatus) === parseInt(obj.id))?.name}</span>
      </div>
    )
  }

]
